body {
  background: #eee;
}

img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}

p {
  max-width: 760px;
}

.App {
  font-family: Inter,-apple-system,sans-serif;
  font-size: 24px;
  line-height: 1.6em;
  color: #111;
}

.App-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px 0;
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}

.flex-item {
  padding: 0 20px;
}

.App-header {
  background-color: #4a7f79;
  color: #fff;
  text-align: center;
}

.App-header img {
  width: 400px;
}

.App-heading {
  font-family: 'Amatic SC', sans-serif;
  font-weight: bold;
  font-size: 144px;
  line-height: 1.01em;
  margin: 0.6em 0 0;
}

.App-about {
  background: #fff;
  padding-bottom: 40px;
}

.App-link {
  color: #4a7f79;
}

.App-footer {
  background: #eee;
  padding-bottom: 40px;
  text-align: center;
}

.App-footer a {
  color: #333;
  text-decoration: none;
  display: inline-block;
  margin: 0 20px;
}



@media only screen and (max-width: 880px) {
  .flex {
    display: block;
  }
  
  .App-heading {
    margin: 0;
  }
}
